@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

.App {
  width: 95%;
  padding: 0 15px;
  display: flex;
  flex-direction: column;
  gap: 20px;
 font-family: poppins;
}

.title {
  font-weight: 700;
}

.slider {
  width: 100%;
}

.lables {
  display: flex;
  justify-content: space-between;
}

.tenureConatiner {
  display: flex;
  justify-content: space-between;
  gap: 10px
}

.tenure {
  width: 15%;
  height: 50px;
  border: none;
  border-radius: 100px;
  background-color: lightgray;
}

.selected {
  background-color: blueviolet;
  color: white;
}